<template>

	<view class="body">
		<view class="head">
			<view class="head_back" >
				<image src="@/static/img/back.png" mode="" @click="navigateBack()"></image>
			</view>
			<view class="head_center hei_38_bold">流水明细</view>
			<view class=" head_right">
				<image src="@/static/lsimg/sousuo.png" mode="" @click="tan"></image>
			</view>
		</view>

		<view class="zi_body ">
			<view class="liushui_list">
				<view class="liushui_item" v-for="(item,index) in 13">
					<view class="liushui_item_top">
						<text class="hei_28_bold">提现</text>
						<text class="qian_24">2020-10-14 13:42:03</text>
						</view>
						<view class="liushui_item_center hui_26">
							<text>20200830122927442 </text>
							<text class="hong_28">￥100.00 </text>
							</view>
							<view class="liushui_item_bottom qian_26">
								备注：购买分站
								</view>
					
				</view>


			</view>
		</view>

		<uni-popup type="bottom" ref='popup'>
			<view class="tan">
				<view class="tan_title hei_30">
					查询条件
				</view>
				<view class="tan_list hei_26">
					<view class="tan_list_top" @click="zhuan_togg">
						<text>{{zhuanchang_txt==""?'订单类型':zhuanchang_txt}}</text>
						<image src="@/static/lsimg/xuan_xia.png" mode=""></image>
					</view>
					<view class="tan_list_bottom zhuanchang_arry hei_26" v-if="zhuan_show">
						<text v-for="(item,index) in zhuanchang" :class="['' ,index==xuanzc?'xuanzhong': '']" @click="xuanzhuangchang(index,item)">
							{{item}}</text>
					</view>
					<button type="" class="zhuanchang_quer_ding bai_30" @click="zhuanchang_go" v-if="zhuan_show">确定</button>
				</view>
				<view class="tan_list hei_26">
					<view class="tan_list_top">
						<picker @change="change" class='aaaaaa' mode="date"><text class="dizhi">{{time}}</text></picker>
						
						<image src="@/static/lsimg/xuan_xia.png" mode=""></image>
					</view>
				</view>
		

				<view class="tan_buttom " v-if="!zhuan_show">
					<button type="" class="quxiao hong_30" @click="quxiao_btn">取消搜索</button>
					<button type="" class="queren bai_30" @click="queding_btn">确认搜索</button>
				</view>

			</view>
		</uni-popup>


	</view>



</template>

<script>
	import unpopup from '@/components/uni-popup/uni-popup-share.vue'
	import pickerAddress from '@/components/pickerAddress/pickerAddress.vue'
	export default {
		components: {
			unpopup,
			pickerAddress
		},



		data() {
			return {
				time: '创建时间',
				zhuanchang: ['余额充值', '订单支付', '提现'],
				zhuanchang_txt: '订单类型',
				zhuanchang_txt2: '',
				zhuan_show:false,
				xuanzc:'9999'
			}
		},
		created() {

		},
		//下拉刷新
		onPullDownRefresh: function() {
			// this.getList();

			setTimeout(function() {
				uni.showToast({
					title: '没有更多内容了',
					duration: 2000,
					icon: "none"
				});
				uni.stopPullDownRefresh();
			}, 1000);
		},
		// stopPullDownRefresh:function(){

		// },
		onShow() {

		},
		methods: {
			//上拉加载
			onReachBottom() {
				uni.showToast({
					title: '没有更多内容了',
					duration: 2000,
					icon: "none"
				});
			},
			navigateBack() {
				uni.navigateBack()
			},
	
			tan() {
				this.$refs.popup.open()

			},
			// 时间
			change(data) {
				console.log(data.detail.value
				)
				this.time = data.detail.value
			},
			// 选择类型
			xuanzhuangchang(index, item) {
				this.xuanzc = index
				this.zhuanchang_txt2 = item
			},
			// 类型确定
			zhuanchang_go() {
				this.zhuanchang_txt = this.zhuanchang_txt2
				this.zhuan_show = false
			},
			zhuan_togg() {
				this.zhuan_show = !this.zhuan_show
			},
			// 取消搜索
			quxiao_btn() {
				this.zhuanchang_txt = '订单类型'
				this.time = '创建时间'
				this.$refs.popup.close()
			},
			// 确定搜索
			queding_btn() {
				console.log(this.time, this.zhuanchang_txt)
				this.$refs.popup.close()
			}

		}
	}
</script>

<style>


	.tab_top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 10rpx 38rpx;
		background-color: #FFFFFF;
		position: fixed;
		top: 100rpx;
		width: 100%;
		box-sizing: border-box;
		z-index: 9;
	}

	.head_right image {
		width: 40rpx;
		height: 41rpx;
	}

	.liushui_list {
		padding: 0 30rpx;

	}
	.liushui_item{
		border-bottom: 2rpx solid #e5e5e5;
		padding: 14rpx 0;
	}
.liushui_item_top,.liushui_item_center{
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.liushui_item_center{
	margin:13rpx 0 20rpx ;
}




	.tan {
		width: 100%;
		height: 758rpx;
		background-color: #FFFFFF;
		padding: 0 23rpx;
		box-sizing: border-box;
		position: relative;
	}

	.tan_title {
		text-align: center;
		line-height: 120rpx;
	}

	.tan_list {}

	.tan_list_top {
		display: flex;
		align-items: center;
		border-bottom: 2rpx solid #E8E8E8;
		height: 98rpx;
		justify-content: space-between;

	}

	.tan_list image {
		width: 22rpx;
		height: 12rpx;
	}

	.tan_buttom {
		display: flex;
		align-items: center;
		position: absolute;
		bottom: 50rpx;

	}

	.aaaaaa {
		min-width: 650rpx;
		display: inline-block;
	}

	button {
		margin: 0;
		border: none;
	}

	button::after {
		content: none;
	}

	.quxiao {
		width: 342rpx;
		height: 88rpx;
		background-color: #ffd7dc;
		border-radius: 44rpx 0rpx 0rpx 44rpx;
		line-height: 88rpx;
	}

	.queren {
		width: 342rpx;
		height: 88rpx;
		line-height: 88rpx;
		background-color: #f43a51;
		border-radius: 0rpx 44rpx 44rpx 0rpx;
	}

	.zhuanchang_arry {
		display: flex;
		margin-top: 20rpx;
		flex-wrap: wrap;
	}

	.zhuanchang_arry text {
		border: 2rpx solid #E8E8E8;
		border-radius: 20rpx;
		padding: 8rpx 20rpx;
		display: inline-block;
		margin-right: 28rpx;
		margin-bottom: 15rpx;
	}

	.zhuanchang_quer_ding {
		height: 88rpx;
		background-color: #f43a51;
		border-radius: 44rpx;
		line-height: 88rpx;
		position: absolute;
		bottom: 50rpx;
		width: 691rpx;
		box-sizing: border-box;
	}

	.xuanzhong {
		color: #f43a51;
		border: 2rpx solid #f43a51 !important;
	}
</style>
